raul-list-item {
  display: block;
  border-bottom-width: 1px;
  border-color: #EBEDEE;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 0.875rem;
}

raul-list-item.raul-list-item--movable, raul-list-item.raul-list-item--movable-by-handle {
  padding-top: 0;
  padding-bottom: 0;
}

raul-list-item.raul-list-item--movable:hover, raul-list-item.raul-list-item--movable-by-handle:hover {
  background-color: #E5F4FF;
}

raul-list-item.raul-list-item--movable:hover raul-handle, raul-list-item.raul-list-item--movable-by-handle:hover raul-handle {
  opacity: 1;
}

raul-list-item.raul-list-item--movable:hover {
  cursor: move;
}

raul-list-item.raul-list-item--movable-by-handle:hover raul-handle {
  cursor: move;
}